body {
  background-color: #44403c;
}
.App {
    font-family: sans-serif;
    display: flex;
    justify-content: center;
  }
  p {
    color: rgb(218, 218, 148);    
  }
  .clock {
    width: 300px;
    height: 300px;
    border-radius: 50%;
    position: absolute;
    top: 10%;
    left: 30%;   
    transform: translate(-50%, - 50%);
    box-shadow: 0 2px 30px #fef08a;
    font-size: 24px;
    color: #fef08a;
    text-align: center;
    margin: 10px 150px;
  }
  
  .clock::after {
    background: #aaa;
    content: "";
    width: 12px;
    height: 12px;
    border-radius: 50%;
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: 2px solid #fff;
  }
  .hour_hand {
    position: absolute;
    width: 6px;
    height: 60px;
    background: #fef08a;
    top: 30%;
    left: 49%;
    transform-origin: bottom;
  }
  .min_hand {
    position: absolute;
    width: 4px;
    height: 80px;
    background: #fef08a;
    top: 22.5%;
    left: 49%;
    transform-origin: bottom;
  }
  .sec_hand {
    position: absolute;
    width: 2px;
    height: 118px;
    background: red;
    top: 10.5%;
    left: 50%;
    transform-origin: bottom;
  }
  .clock span {
    position: absolute;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 700;
  }
  .twelve {
    top: 10px;
    left: 46%;
  }
  .one {
    top: 10%;
    right: 26%;
  }
  .eleven {
    top: 10%;
    left: 26%;
  }
  .two {
    top: 25%;
    right: 10%;
  }
  .three {
    right: 10px;
    top: 46%;
  }
  .four {
    right: 30px;
    top: 67%;
  }
  .five {
    right: 78px;
    top: 80%;
  }
  .six {
    bottom: 10px;
    left: 50%;
  }
  .seven {
    left: 80px;
    top: 82%;
  }
  .eight {
    left: 30px;
    top: 67%;
  }
  .nine {
    left: 10px;
    top: 46%;
  }
  .ten {
    top: 25%;
    left: 10%;
  }
  